@import '../../global.scss';

nav {
    position: fixed;
    background: $navbar;
    color: $brandcolor;
    box-shadow: 0px 4px 4px 0px rgba($color: #000000, $alpha: 0.14);
    width: 100%;
    height: 64px;
    z-index: 1000;

    display: flex;
    align-items: center;
    justify-content: center;
}



svg {
    height: 60%;
    width: 30px;
    fill: $brandcolor;
}
.logo {
    margin: 0;
    padding: 0;
    width: 50%;
    color: $brandcolor;
    fill: $brandcolor;
    text-decoration: none;
}

button {
    background: none;
    border: none;
    padding: 0;
    height: 100%;
    width: 25%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
button span {
    display: block;
    height: 2px;
    width: 30px;
    margin: 3px;
    background: $brandcolor;
}

//MENU MENU MENU MENU MENU MENU MENU



.left-active {
    animation: showLeft 0.6s forwards;
}
.button-active {
    background: $brandcolor;
    svg {
        fill: $navbar;
    }
    span {
        background: $navbar;
    }
}



.right-active {
    animation: showRight 0.6s forwards;
}


.nav-left {
    padding-top: 10px;
    background: $brandcolor;
    width: 75%;
    max-width: 400px;
    position: fixed;
    top: 0;
    bottom: 0;
    left: -100%;
    box-shadow: 4px 4px 4px 0px rgba($color: #000000, $alpha: 0.14);

}

.nav-right {
    padding-top: 64px;
    background: $brandcolor;
    width: 75%;
    max-width: 400px;
    position: fixed;
    top: 0;
    bottom: 0;
    right: -100%;
    box-shadow: 0px 4px 4px 4px rgba($color: #000000, $alpha: 0.14);    
}

@keyframes showLeft {
    from {
        left: -100;
    }
    to {
        left: 0;
    }
}
@keyframes showRight {
    from {
        right: -100%;
    }
    to {
        right: 0;
    }
}

@media screen and (min-width: 900px) {
    nav {
        justify-content: flex-start;
    }
    .logo {
        width: auto;
        text-align: left;
        padding: 0 25px;
    }
    button {
        width: 5%;
    }
    #left-icon {
        background: $brandcolor;
        width: 64px;
        svg {
            fill: $navbar;
        }
    }
    #right-icon {
        display: none;
    }
    .RightSlide {

    }
}